/* eslint-disable react/prop-types, react/destructuring-assignment, react/jsx-props-no-spreading */
import React from 'react';
import { graphql } from 'gatsby';

import EpisodeBase, {
  Choices as ChoicesBase,
} from 'gatsby-plugin-metakronisms-content/dist/Episode';
import Layout from '../Layout/Layout';
import Card from '../Card/Card';

// import TypistSlide from '../Slides/TypistSlide';

import styles from './Episode.module.scss';
import choicesStyles from './Choices.module.scss';

const Choices = (props) => (
  <ChoicesBase
    {...props}
    classes={{
      ...(props.classes || {}),
      ...choicesStyles,
    }}
  />
);

Choices.displayName = 'MKChoices';

const Episode = (props) => (
  <Layout>
    <EpisodeBase
      // eslint-disable-next-line react/jsx-props-no-spreading
      {...props}
      classes={{
        ...styles,
        // eslint-disable-next-line
        ...(props.classes || {}),
      }}
      components={{
        Card,
        Choices,
        // TypistSlide,
      }}
    />
  </Layout>
);

Episode.displayName = 'MKEpisode';

export default Episode;

export const query = graphql`
query allEpisodeMdx ($seasonNr: Int = 0, $episodeNr: Int = 0) {
  episodeMdx: allMdx(filter: {fields: {seasonNr: {eq: $seasonNr}, episodeNr: {eq: $episodeNr}}}) {
    nodes {
      parent {
        ... on File {
          name
        }
      }
      frontmatter {
        title
        choices {
          target
          title
        }
      }
      body
    }
  }
}
`;
